@import url("https://fonts.googleapis.com/css?family=Press+Start+2P");
@import url("https://fonts.googleapis.com/css?family=VT323");

.App {
  text-align: center;
  font-family: "Press Start 2P", "VT323";
  color: white;
}

* {
  box-sizing: border-box;
}

body {
  overflow: hidden;
}

.overlay {
  opacity: 100;
  width: 100%;
  height: 100%;
  z-index: 10;
  top: 0;
  left: 0;
  position: fixed;
}
body {
  overflow-y: scroll;
}

.mute-button {
  position: absolute;
  top: 800px;
  left: 15%;
  background-color: rgba(0, 0, 0, 0.7);
  border: none;
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
  border-radius: 5px;
}
.moving-background {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("./assets/background/2-bg.gif");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: 40% 40%;
}
.scanline {
	animation: scroll 10s 5s infinite;
	background: -moz-linear-gradient(top,  rgba(0,221,0,0) 0%, rgba(0,221,0,1) 50%, rgba(0,221,0,0) 100%);
	background: -webkit-linear-gradient(top,  rgba(0,221,0,0) 0%,rgba(0,221,0,1) 50%,rgba(0,221,0,0) 100%);
	background: linear-gradient(to bottom,  rgba(0,221,0,0) 0%,rgba(0,221,0,1) 50%,rgba(0,221,0,0) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0000dd00', endColorstr='#0000dd00',GradientType=0 );
	display: block;
	height: 20px;
	opacity: 0.05;
	position: absolute;
		left: 0;
		right: 0;
		top: -5%;
	z-index: 2;
}
.flickering-lines {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background: black;
}

.flickering-lines::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(90deg, rgba(0, 255, 0, 0) 50%, rgba(0, 255, 0, 0.2) 50%);
  animation: flicker 0.1s infinite;
}

@keyframes flicker {
  0%, 19.999%, 22%, 62.999%, 64%, 64.999%, 70%, 100% {
    opacity: 1;
  }
  20%, 21.999%, 63%, 63.999%, 65%, 69.999% {
    opacity: 0;
  }
}
.terminal {
  position: absolute;
  top: 900px;
  left: 100px;
  width: 50%;
  height: 70%;
  min-height: 700px;
  
  background-color: #000;
  border: 2px solid #222;
  border-radius: 5px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  font-family: "Courier New", Courier, monospace;
  cursor: move;
}

.terminal-header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 30px;
  background-color: #222;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.terminal-button {
  width: 15px;
  height: 15px;
  margin: 0 5px;
  border-radius: 50%;
}

.red {
  background-color: #f44;
}

.yellow {
  background-color: #fd0;
}

.green {
  background-color: #3c3;
}

.terminal-body {
  margin: 20px;
  color: #fff;
  line-height: 1.2;
}
#cursor {
  display: inline-block;
  width: 10px;
  height: 20px;
  background-color: white;
  animation: blink 1s infinite;
}

@keyframes blink {
  50% {
    opacity: 0;
  }
}

.Mint:hover {
  background-color: #D6517D;
  color:'green';
  cursor: pointer;
}
@keyframes vibrate {
  0% { transform: translate(0, 0); }
  25% { transform: translate(5px, -5px); }
  50% { transform: translate(0, 0); }
  75% { transform: translate(-5px, 5px); }
  100% { transform: translate(0, 0); }
}
@keyframes glitch {
  0% {
    transform: translate(0, 0);
  }
  20% {
    transform: translate(1px, -1px);
  }
  40% {
    transform: translate(-1px, 1px);
  }
  60% {
    transform: translate(1px, 1px);
  }
  80% {
    transform: translate(-1px, -1px);
  }
  100% {
    transform: translate(0, 0);
  }
}

.roadmap-container {
  animation: glitch 1s infinite;
}
.tv {
  position: relative;
  display: inline-block;
}

.tv::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.2;
  z-index: 1;
}

.screen {
  position: relative;
  z-index: 2;
}

.static {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('https://i.imgur.com/0oAzd.png');
  background-repeat: repeat;
  opacity: 0.2;
  z-index: 3;
  animation: static 0.1s linear infinite;
}

@keyframes static {
  0% {
    transform: translate3d(0, 0, 0);
  }
  20% {
    transform: translate3d(-2px, -2px, 0);
  }
  40% {
    transform: translate3d(2px, 2px, 0);
  }
  60% {
    transform: translate3d(-2px, -2px, 0);
  }
  80% {
    transform: translate3d(2px, 2px, 0);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}



